import React from "react";
import { Link } from "react-router-dom"
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import InputGroup from 'react-bootstrap/InputGroup';
import Form from 'react-bootstrap/Form';
import Card from 'react-bootstrap/Card';
import Pagination from 'react-bootstrap/Pagination';

import { AuthButton } from "../auth/AuthButton";
import ProvideTheme from "./ProvideTheme";

function Home() {
    return (
        <ProvideTheme>
            <Container>
                <Row>
                    <Col className="text-center">
                        <AuthButton />
                        <div className="jumbotron text-center">
                            <h2 >Are You Having Software Talent Hiring Issues Which Keep You up at Night? We Can Help!</h2>
                            <p>We help software talents and recruiters to connect with one another</p>
                            <p><Link to={"/recruiter/auth/signin"} className="btn btn-secondary btn-sm m-2">Talent</Link><Link to={"/recruiter/auth/signin"} className="btn btn-success btn-sm m-2">Recruiter</Link></p>
                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col className="d-flex justify-content-around">
                        <InputGroup className="mb-3 m-2">
                            <Form.Control
                                placeholder="Search for jobs"
                                aria-label="Search for jobs"
                                aria-describedby="basic-addon2"
                            />
                            <Button variant="outline-secondary" id="button-addon2">
                                Search
                            </Button>
                        </InputGroup>

                    </Col>
                </Row>
                <Row>
                    <Col>
                        <h4>Jobs</h4>

                        <Row xs={1} md={2} className="g-4">
                            {Array.from({ length: 8 }).map((_, idx) => (
                                <Col>
                                    <Card key={idx + "p"}>
                                        <Card.Img variant="top" src="https://via.placeholder.com/50x50" />
                                        <Card.Body>
                                            <Card.Title>Card title</Card.Title>
                                            <Card.Text>
                                                This is a longer card with supporting text below as a natural
                                                lead-in to additional content. This content is a little bit
                                                longer.
                                            </Card.Text>
                                        </Card.Body>
                                    </Card>
                                </Col>
                            ))}
                        </Row>

                        <Row>
                            <Col>
                                {
                                    (() => {
                                        let active = 2;
                                        let items = [];
                                        for (let number = 1; number <= 4; number++) {
                                            items.push(
                                                <Pagination.Item key={number + "x"} active={number === active}>
                                                    {number}
                                                </Pagination.Item>,
                                            );
                                        }
                                        return <Pagination >{items}</Pagination>;
                                    })()

                                }
                            </Col>

                        </Row>

                    </Col>
                </Row>

            </Container>
        </ProvideTheme>
    )
}

export default Home;